.coaches {
  min-width: 320px;
  padding: 0 30px;

  @include vp-1199 {
    padding: 0;
  }
}

.coaches__wrapper {
  padding: 57px 60px 90px;

  @include vp-1199 {
    padding: 51px 101px 100px;
  }

  @include vp-767 {
    max-width: 564px;
    padding: 35px 47px 70px;
  }
}

.coaches__title {
  margin-bottom: 47px;
  margin-left: 2px;

  @include vp-1199 {
    margin-bottom: 31px;
    margin-left: -6px;
    text-align: center;
  }

  @include vp-767 {
    margin-bottom: 41px;
  }
}

.coaches__content {
  position: relative;
}

.coaches__swiper {
  margin: -28px;
  padding: 28px;

  @include vp-767 {
    margin: -15px;
    padding: 15px;
  }
}

.coaches__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.coaches__item {
  aspect-ratio: 260/294;

  @include vp-1199 {
    aspect-ratio: 268/294;
  }

  @include vp-767 {
    aspect-ratio: 226/274;
  }
}

.coaches__toggle {
  z-index: 1;
  position: absolute;
  top: 28px;
  bottom: 28px;
  display: flex;
  width: 40px;
  padding: 0;
  background-color: $color-transparent;
  border: 0;
  cursor: pointer;

  &:disabled,
  &--disabled {
    opacity: 0.5;
    pointer-events: none;
    cursor: not-allowed;
  }

  &--previous {
    left: -70px;
  }

  &--next {
    right: -70px;
  }

  @include vp-767 {
    top: 15px;
    bottom: 15px;
    width: 30px;

    &--previous {
      left: -47px;
    }

    &--next {
      right: -47px;
    }
  }
}

.coaches__toggle:hover .coaches__toggle-icon,
.coaches__toggle:focus-visible .coaches__toggle-icon {
  stroke: $color-dark-cerulean;
}

.coaches__toggle:active .coaches__toggle-icon {
  opacity: 0.8;
  stroke: $color-black-russian;
}

.coaches__toggle-icon {
  width: 24px;
  height: 34px;
  margin: auto 0;
  stroke: $color-dark-gray;

  &--previous {
    margin-left: 10px;
  }

  &--next {
    margin-left: 5px;
    transform: rotate(180deg);
  }

  @include vp-767 {
    width: 10px;
    height: 20px;

    &--previous {
      margin-left: 15px;
    }

    &--next {
      margin-left: 5px;
    }
  }
}

/*   JavaScript */

.js-exception .coaches__swiper {
  overflow: initial;
}

.js-exception .coaches__list {
  display: grid;
  grid-template-columns: repeat(4, minmax(200px, 260px));
  column-gap: 40px;
  row-gap: 40px;
  margin: 0;
  padding: 0;
  transform: none;

  @include vp-1199 {
    grid-template-columns: repeat(2, minmax(268px, 1fr));
    column-gap: 30px;
    row-gap: 30px;
  }

  @include vp-767 {
    grid-template-columns: minmax(226px, 1fr);
    column-gap: 0;
    row-gap: 20px;
  }
}

.js-exception .coaches__toggle {
  display: none;
}
